.contact {
    position: relative;
    z-index: 400;
}

.content--open .contact {
    height: auto;
}

.contact__top {
    background-color: var(--light);
    height: 30%;
}

.contact__bottom {
    background-color: var(--contact);
    height: 70%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.contact__title {
    top: 30%;
    left: 70%;
    -webkit-transform: translate(-50%, -80%);
        -ms-transform: translate(-50%, -80%);
            transform: translate(-50%, -80%);
}

.contact__title .title__letters--top {
    -webkit-transform: translateY(-100vh);
        -ms-transform: translateY(-100vh);
            transform: translateY(-100vh);
}

.contact__title .title__letters--bottom {
    -webkit-transform: translatex(100vw);
        -ms-transform: translatex(100vw);
            transform: translatex(100vw);
}

.contact__box {
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 80px;
    opacity: 0;
    -webkit-transition: opacity .2s ease;
    -o-transition: opacity .2s ease;
    transition: opacity .2s ease;
    font-size: 1.2rem;
}

.contact__box:nth-child(1) {
    margin-left: 100px;
    -ms-flex: 2 1;
        flex: 2 1;
}

.contact__box--hidden {
    opacity: 1;
}

.contact__text {
    font-weight: 700;
}

.contact__details {
    margin: 20px;
}

.contact__social {
    list-style: none;
    margin-top: 20px;
}

.contact__social li {
    margin: 10px;
    padding: 5px;
}

.contact__social li a {
    padding: 2px;
    background-color: var(--light);
}


/*media queries*/
@media only screen and (max-width: 1400px) {
    
    .contact__box {
        font-size: 1rem;
    }
    
    .contact__box:nth-child(1) {
        margin-left: 50px;
    }   
    
    .contact__social li {
        margin: 5px;
    }
    
}


@media only screen and (max-width: 1023px) {
    
}

@media only screen and (max-width: 767px) {
    
    .contact {
        height: 100vh;
    }
    
    .contact__title {
        top: 26%;
        left: 65%;
    }
    
    .contact__bottom {
        height: 70%;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }
    
    .contact__box {
        font-size: .8rem;
        -ms-flex: none;
            flex: none;
        padding: 15px 40px;
    }
    
    .contact__box:nth-child(1) {
        -ms-flex: none;
            flex: none;
        margin-left: 0;
        margin-top: 20px;
    }
    
    .contact__box:nth-child(2) {
        padding-top: 0;
    }
    
    .contact__social {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        margin-top: 10px;
    }  
    
    .contact__text,
    .contact__details {
        text-align: center;
    }
    

    
    
}



