
.about__top {
    background-color: var(--about);
    height: 40%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    position: relative;
}

.about__bottom {
    background-color: var(--light);
    height: 60%;
    display: -ms-flexbox;
    display: flex;
}

.about__title {
    top: 35%;
    left: 70%;
    -webkit-transform: translate(-50%, -60%);
        -ms-transform: translate(-50%, -60%);
            transform: translate(-50%, -60%);
}

.about__title .title__letters--top {
    -webkit-transform: translateY(-60vh);
        -ms-transform: translateY(-60vh);
            transform: translateY(-60vh);
}

.about__title .title__letters--bottom {
    -webkit-transform: translatex(100vw);
        -ms-transform: translatex(100vw);
            transform: translatex(100vw);
}

.about__box {
    opacity: 1;
    -webkit-transition: opacity .2s ease;
    -o-transition: opacity .2s ease;
    transition: opacity .2s ease;
}

.about__box--hidden {
    opacity: 0;
}

.about__photo-wrapper {
    position: relative;
    height: 100%;
    width: 15%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.about__gradient {
    position: absolute;
    top: 0;
    right: 0;
    background: -webkit-gradient(linear, right, left , from(rgba(0, 0, 0, 0.01)), to(var(--about)));
    background: -webkit-gradient(linear, right top, left top, from(rgba(0, 0, 0, 0.01)), to(var(--about)));
    background: -webkit-linear-gradient(right, rgba(0, 0, 0, 0.01), var(--about));
    background: -o-linear-gradient(right, rgba(0, 0, 0, 0.01), var(--about));
    background: linear-gradient(to left, rgba(0, 0, 0, 0.01), var(--about));
    z-index: 100;
    width: 100%;
    height: 100%;
}

.about__photo {
    height: 100%;
    width: 100%;
    z-index: 90;
}

.about__greet {
    position: absolute;
    top: 50%;
    left: 10%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    z-index: 200;
}

.about__greet h3,
.about__greet h2{
    font-weight: 500;
}

.about__greet h3 {
    font-size: 1.8rem;
}

.about__greet h2 {
    font-size: 2rem;
}

.about__greet h3 span {
    background-color: var(--light);
    padding: 0 2px;
}

.about__description {
    font-size: 1.2rem;
    -ms-flex: 2 1;
        flex: 2 1;
    padding-right: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 50px 100px;
}

.about__more {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 60px;
    padding-right: 100px;
    font-size: 1.2rem;
}

.about__more p {
    margin-top: 30px;
}

.about__link {
    position: relative;
    z-index: 20;
}

.about__link:before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50%;
    background-color: var(--main);
    z-index:-1;
}



/*media queries*/
@media only screen and (max-width: 1400px) {
    
    .about__greet h3 {
        font-size: 1.1rem;
    }

    .about__greet h2 {
        font-size: 1.3rem;
    }
    
    .about__description,
    .about__more {
        font-size: .9rem;
    }
    
    .about__more {
        padding: 50px;
    }
    
    .about__photo-wrapper {
        width: 17%;
    }
    
}


@media only screen and (max-width: 1023px) {
    
    .about__photo-wrapper {
        width: 25%;
    }
    
}

@media only screen and (max-width: 767px) {
    
    .about__top {
        height: 70vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: -1;
    }
    
    .content--open .about__top {
        position: relative;
    }
    
    .about__bottom {
        -ms-flex-direction: column;
            flex-direction: column;
        margin-top: 70vh;
        height: auto;
    }
    
    .content--open .about__bottom {
        margin-top: 0;
    }
    
    .about__title {
        top: 65vh;
        left: 40%;
    }
    
    .about__title .title__letters--top {
        -webkit-transform: translateY(-90vh);
            -ms-transform: translateY(-90vh);
                transform: translateY(-90vh);
    }
    
    .greet__animate {
        -webkit-transform: translate(0,0);
            -ms-transform: translate(0,0);
                transform: translate(0,0);
        opacity: 1;
        -webkit-transition: all .4s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
        -o-transition: all .4s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
        transition: all .4s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    }
    
    .greet__animate--on {
        opacity: 0;
        -webkit-transform: translate(-50vw, -50vh);
            -ms-transform: translate(-50vw, -50vh);
                transform: translate(-50vw, -50vh);
    }
    
    .greet__animate.about__box--hidden {
        opacity: 0;
    }
    
    .about__photo-wrapper {
        width: 80%;
    }
    
    .about__photo {
        width: 100%;
    }
    
    .about__greet {
        left: 30px;
    }   
    
    .about__greet h2 {
        font-size: 1.1rem;
    }
    
    .about__greet h3 {
        font-size: 1rem;
    }
    
    .about__description {
        font-size: .85rem; 
        padding: 30px;
        -ms-flex: none;
            flex: none;
        margin-top: 30px;
    }
    
    .about__more {
        -ms-flex: none;
            flex: none;
        padding: 30px;
        padding-bottom: 50px;
    }
    
    
}



