.header__logo {
    font-weight: 700;
    position: absolute;
    background-color: transparent;
    top: 0;
    left: 60px;
    height: 60px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    z-index: 1000;
    -webkit-transition: -webkit-transform .5s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    transition: -webkit-transform .5s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    -o-transition: transform .5s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    transition: transform .5s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    transition: transform .5s cubic-bezier(0.7, 0, 0.3, 1) 0ms, -webkit-transform .5s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    cursor: pointer;
}

.header__logo:after {
    content: '';
    display: block;
    position: absolute;
    top: 35%;
    right: -8px;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: var(--font);
}

.header__logo--open {
    -webkit-transform: translate(150%, 100%);
        -ms-transform: translate(150%, 100%);
            transform: translate(150%, 100%);
}

.header__nav-icon {
    position: absolute;
    top: 50%;
    left: 50px;
    z-index: 1000;
    -webkit-transition: -webkit-transform .3s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    transition: -webkit-transform .3s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    -o-transition: transform .3s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    transition: transform .3s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    transition: transform .3s cubic-bezier(0.7, 0, 0.3, 1) 0ms, -webkit-transform .3s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    cursor: pointer;
}

.header__nav-icon--open {
    -webkit-transform: translateX(40px);
        -ms-transform: translateX(40px);
            transform: translateX(40px);
}

.header__icon-line {
    display: block;
    height: 2px;
    background-color: var(--font);
    margin-top: 4px;
    -webkit-transition: all .5s cubic-bezier(0.7, 0, 0.3, 1);
    -o-transition: all .5s cubic-bezier(0.7, 0, 0.3, 1);
    transition: all .5s cubic-bezier(0.7, 0, 0.3, 1);
}

.header__icon-line:nth-child(1) {
    width: 20px;
    margin-top: 0;
}

.header__icon-line:nth-child(2) {
    width: 25px;
}

.header__icon-line:nth-child(3) {
    width: 15px;
    -webkit-transition: all .05s cubic-bezier(0.7, 0, 0.3, 1);
    -o-transition: all .05s cubic-bezier(0.7, 0, 0.3, 1);
    transition: all .05s cubic-bezier(0.7, 0, 0.3, 1);
}


.nav-hover .header__icon-line:nth-child(1) {
    width: 25px;
}

.nav-hover .header__icon-line:nth-child(2) {
    width: 40px;
}

.nav-hover .header__icon-line:nth-child(3) {
    width: 20px;
}

.header__nav-icon--open .header__icon-line:nth-child(1) {
    width: 25px;
    -webkit-transform: rotate(225deg);
        -ms-transform: rotate(225deg);
            transform: rotate(225deg);
}

.header__nav-icon--open .header__icon-line:nth-child(2) {
    margin-top: -2px;
    -webkit-transform: rotate(-225deg);
        -ms-transform: rotate(-225deg);
            transform: rotate(-225deg);
}

.header__nav-icon--open .header__icon-line:nth-child(3) {
    width: 0;
}

.header__language {
    font-weight: 700;
    letter-spacing: 2px;
    font-size: .6rem;
    position: absolute;
    top: 0;
    right: 60px;
    height: 60px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    z-index: 1000;
    -webkit-transition: all .5s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    -o-transition: all .5s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    transition: all .5s cubic-bezier(0.7, 0, 0.3, 1) 0ms;
    color: var(--gray);
}

.header__language span {
    cursor: pointer;
}

.header__language span:first-child {
    margin-right: 10px;
}

.header__language--open {
    -webkit-transform: translate(-40px ,90%);
        -ms-transform: translate(-40px ,90%);
            transform: translate(-40px ,90%);
}

.header__language .active {
    color: var(--font);
}


/*media queries*/
@media only screen and (max-width: 1400px) {
    
    .header__logo {
        left: 40px;
        height: 40px;
    }
    
    .header__nav-icon {
        left: 30px;
    }
    
    .header__language {
        right: 40px;
        height: 40px;
    }
    
    .header__language--open {
        -webkit-transform: translate(-40px ,100%);
            -ms-transform: translate(-40px ,100%);
                transform: translate(-40px ,100%);
    }
    
}


@media only screen and (max-width: 1023px) {
    
}



@media only screen and (max-width: 767px) {
    
    .header__language {
        font-size: .6rem;
        top: calc(100vh - 65px);;
        right: calc(100% - 100px);
        height: 40px;
        -webkit-transform: translateY(30px);
            -ms-transform: translateY(30px);
                transform: translateY(30px);
        opacity: 0;
        -webkit-transition-duration: .3s;
             -o-transition-duration: .3s;
                transition-duration: .3s;
        -webkit-transition-delay: .3s;
             -o-transition-delay: .3s;
                transition-delay: .3s;
    }
    
    .header__language--open {
        -webkit-transform: translateY(0);
            -ms-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
    
    .header__logo {
        top: 30px;
    }
    
    .header__logo--open {
        -webkit-transform: translate(0);
            -ms-transform: translate(0);
                transform: translate(0);
    }
    
    .header__nav-icon {
        position: fixed;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        top: 30px;
        left: calc(100% - 70px);
        background-color: #fff;
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
    
    .nav-hover .header__icon-line:nth-child(1) {
        width: 20px;
    }

    .nav-hover .header__icon-line:nth-child(2) {
        width: 25px;
    }

    .nav-hover .header__icon-line:nth-child(3) {
        width: 15px;
    }
    
    .header__nav-icon--open .header__icon-line:nth-child(1) {
        width: 20px;
        -webkit-transform: rotate(225deg);
            -ms-transform: rotate(225deg);
                transform: rotate(225deg);
    }

    .header__nav-icon--open .header__icon-line:nth-child(2) {
        width: 20px;
        -webkit-transform: rotate(-225deg);
            -ms-transform: rotate(-225deg);
                transform: rotate(-225deg);
    }

    .header__nav-icon--open .header__icon-line:nth-child(3) {
        width: 0;
    }
    
    
    .header__nav-icon--open div {
        -webkit-transform: translateY(3px);
            -ms-transform: translateY(3px);
                transform: translateY(3px);
    }
    
    .header__nav-icon--open {
        -webkit-transform: translateX(0);
            -ms-transform: translateX(0);
                transform: translateX(0);
    }
    
    
}


